@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.input-field {
  margin-left: 0.6em;
  margin-right: 0.6em;
}

.input-label {
  display: block;
  margin: 0;
  font-size: 0.6em;
  font-weight: bold;
  /* margin-bottom: 10px; */
}
.input-element {
  border-radius: 4px;
  border: none;
  height: 2em;
  margin-bottom: 12px;
  min-width: -webkit-fill-available;
  padding: 10px;
}
textarea {
  resize: none;
  border-radius: 4px;
  border: none;
  margin-bottom: 12px;
  margin-top: 12px;
  min-width: -webkit-fill-available;
  padding: 2px;
  height: 100px;
  font-family: "Open Sans", sans-serif;
  font-size: 10px;
}
.increment-button {
  float: right;
  background: #69ddff;
  border: none;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  color: white;
  margin-right: 8px;
}
.increment-button:active {
  outline: none;
}
.increment-button:hover {
  opacity: 0.7;
}
.input-small {
  display: block;
  font-size: 0.5em;
  font-weight: bold;
}
.custom{
    margin-bottom:30px;
}
.CustomHeading{
    margin-bottom:12px;
}
.heading{
    font-size:1.2em;
    margin-left:0.6em;
}
.rule{
    background-color: #69DDFF;
    height: 2px;
    border:none;
    margin-bottom:12px;
    margin-left:0.6em;
    margin-right:0.6em;
}
.grid-center{
    display:grid;
    place-items: center;
}
.resume-form{
    background-color: #F2EFEA;
    width:600px;
    padding:0.6rem;
    border-radius: 1rem;
    box-shadow: 4px 4px 4px #C3BAAA;
    font-family: 'Open Sans', sans-serif;
}
.pdfViewer{
    min-width: -webkit-fill-available;
    height: 600px;
    border: none;
}
@media screen and (max-width:600px){
    .resume-form{
        width:80%;
    }
    .pdfViewer{
        width:100%;
        min-width: none;
        height:100px;
    }
}
.grid-center{
    display:grid;
    place-items: center;
}
body {
  font-family: "Poppins", sans-serif;
  font-size:1.2em;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.App{
  margin-top:2rem;
  display:grid;
  place-items: center;
}

